.column {
    margin-top: -1rem;
    margin-bottom: -1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;

    display: flex !important;
    align-items: center !important;
    justify-content: space-around !important;
}

.mainColor {
    flex-direction: row;
}

.similarColor {
    flex-direction: column;
}

.row {
    padding: 0px !important;
}

.color .save-color {
    opacity: 0;
    transition: all 0.2s ease-in-out, opacity 0.1s ease-in-out;
    transform: scale(0.9);
}

.color:hover .save-color {
    opacity: 1;
}

i {
    font-weight: 700;
    font-size: x-large !important;
}

.color i {
    opacity: 0.7 !important;
    color: white;
}

h1,
h2,
h3,
h4,
h5 {
    opacity: 0.7 !important;
    color: white !important;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
}

h3,
h4,
h5 {
    font-weight: 400;
}

input {
    font-size: 1.5rem !important;
}

.search-colors {
    width: 100%;
}

.menu {
    padding-top: 2px !important;
    margin-bottom: 2px !important;
}

html,
body,
#root {
    height: 100% !important;
    margin: 0 !important;
}

#searchForm .ui.category.fluid.search,
#searchForm .ui.icon.input {
    width: 100%;
    height: 100%;
}

.results.transition {
    max-height: 50vh;
    overflow-y: scroll;
}

#myfooter {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 20px;
    height: 50px;
}

#footerline {
    flex-grow: 1;
    margin: 0px 20px;
    /* border-bottom: 2px solid black; */
    height: 6px;
    border-radius: 10px;
}

#footerline {
    /* IE10 Consumer Preview */
    background-image: -ms-linear-gradient(bottom, #ffffff 0%, #00a3ef 100%);

    /* Mozilla Firefox */
    background-image: -moz-linear-gradient(bottom, #ffffff 0%, #00a3ef 100%);

    /* Opera */
    background-image: -o-linear-gradient(bottom, #ffffff 0%, #00a3ef 100%);

    /* Webkit (Safari/Chrome 10) */
    background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0, #ffffff), color-stop(1, #00a3ef));

    /* Webkit (Chrome 11+) */
    background-image: -webkit-linear-gradient(bottom, #ffffff 0%, #00a3ef 100%);

    /* W3C Markup, IE10 Release Preview */
    background-image: linear-gradient(to left, #dddddd 0%, #14d824 100%);
}
